import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { NumberSubscribersComponent } from './number.subscribers.component';
import { TypeSubscribersComponent } from './type.subscribers.component';

@NgModule({
  imports: [

      BrowserModule,
      HttpModule,
      CommonModule,
      FormsModule,
     
    RouterModule.forChild([
        { path: 'number-subscribers', component: NumberSubscribersComponent }, 
        { path: 'type-subscribers', component: TypeSubscribersComponent }
                  
    ])
  ],
  declarations: [
      NumberSubscribersComponent,
      TypeSubscribersComponent           
  ],
  providers: [
    
  ]
})
export class SubscribersModule {}



